* {
  box-sizing: border-box;
  margin: 0;
}

body {
  height: 100vh;
}

#container {
  height: 100%;
  width: 100%;
  background-color: #eee;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  font-family: Verdana, Tahoma, sans-serif;
  display: flex;
}

header, footer {
  height: 75px;
  width: 100%;
}

footer {
  justify-content: center;
  align-items: center;
  display: flex;
}

.github-link {
  color: #000;
  justify-content: center;
  align-items: center;
  gap: 30px;
  font-size: 30px;
  text-decoration: none;
  display: flex;
}

.github-logo {
  height: 45px;
}

.calc {
  height: 600px;
  width: 400px;
  flex-direction: column;
  justify-content: space-between;
  gap: 7.5px;
  display: flex;
}

.calc-row {
  flex: 1;
  justify-content: space-between;
  gap: 7.5px;
  display: flex;
}

.output {
  width: 100%;
  color: #222;
  background-color: #e3e3e3;
  border: 2px solid #bbb;
  border-radius: 25px;
  justify-content: flex-end;
  align-items: center;
  padding: 0 15px;
  font-size: 37.5px;
  display: flex;
}

.btn {
  cursor: pointer;
  color: #222;
  background-color: #bbb;
  border: none;
  border-radius: 25px;
  flex: 1;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-family: inherit;
  font-size: 30px;
  display: flex;
}

.btn:hover {
  background-color: #ccc;
}

.btn:active {
  background-color: #aaa;
}

.xl-btn {
  flex: 2.05;
}

.xxl-btn {
  flex: 3.1;
}

.btn:last-child {
  color: #fff;
  background-color: #f0964b;
}

.btn:last-child:hover {
  background-color: #ffa553;
}

.btn:last-child:active {
  background-color: #da8743;
}

@media (max-width: 400px) and (max-aspect-ratio: 8 / 15) {
  header, footer {
    height: 18.75vw;
  }

  .github-link {
    gap: 7.5vw;
    font-size: 7.5vw;
  }

  .github-logo {
    height: 11.25vw;
  }

  .calc {
    height: 150vw;
    width: 100vw;
    gap: 1.875vw;
  }

  .calc-row {
    gap: 1.875vw;
  }

  .output {
    border-radius: 6.25vw;
    padding: 0 3.75vw;
    font-size: 9.375vw;
  }

  .btn {
    border-radius: 6.25vw;
    font-size: 7.5vw;
  }
}

@media (max-height: 750px) and (min-aspect-ratio: 8 / 15) {
  header, footer {
    height: 10%;
  }

  .github-link {
    gap: 4vh;
    font-size: 4vh;
  }

  .github-logo {
    height: 6vh;
  }

  .calc {
    height: 80%;
    width: 53.33vh;
    gap: 1vh;
  }

  .calc-row {
    gap: 1vh;
  }

  .output {
    border-radius: 3.33vh;
    padding: 0 2vh;
    font-size: 5vh;
  }

  .btn {
    border-radius: 3.33vh;
    font-size: 4vh;
  }
}

